/* Left-aligned home section styling with vertical centering */
.home-section {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: white;
  color: black;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 0 50px;
}

.home-container {
  width: 100%;
  max-width: 1200px;
  margin: 0;
}

.home-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.content-left {
  width: 50%;
  max-width: 600px;
  text-align: left;
}

.content-right {
  width: 50%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-content {
  width: 100%;
  text-align: left;
}

.hero-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: #58a6ff;
  text-align: left;
  white-space: nowrap;
}

.hero-subtitle {
  font-size: 1.5rem;
  font-weight: 500;
  margin-bottom: 20px;
  color: #8b949e;
  text-align: left;
}

.hero-description {
  font-size: 1.125rem;
  line-height: 1.6;
  margin-bottom: 30px;
  color: #c9d1d9;
  text-align: left;
}

.hero-stats {
  display: flex;
  gap: 30px;
  margin-bottom: 30px;
  justify-content: flex-start;
}

.stat-item {
  text-align: left;
}

.stat-number {
  font-size: 2rem;
  font-weight: 700;
  color: #58a6ff;
  display: block;
}

.stat-label {
  font-size: 1rem;
  color: #8b949e;
}

.hero-buttons {
  display: flex;
  gap: 20px;
  justify-content: flex-start;
}

.btn-primary {
  background-color: #238636;
  color: white;
  border: none;
  padding: 12px 30px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn-primary:hover {
  background-color: #2ea043;
}

.btn-secondary {
  background-color: transparent;
  color: #58a6ff;
  border: 2px solid #58a6ff;
  padding: 12px 30px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.btn-secondary:hover {
  background-color: #58a6ff;
  color: #0d1117;
}
