/* Global styles for full browser display */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

#root {
  height: 100%;
  width: 100%;
}

.App {
  min-height: 100vh;
  width: 100%;
  background: #0d1117;
  color: #c9d1d9;
}

/* Remove any default margins */
.App > * {
  margin: 0;
  padding: 0;
}
